
* {
  box-sizing: border-box;
}

html,
body {
  width: 100%;
  height: 100%;
  padding: 10px;
  margin: 0;
  background: #8e2de2; /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #8e2de2, #4a00e0); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #8e2de2, #4a00e0);
  font-family: 'Roboto', sans-serif;
}



.product-list {
  width: 750px;
  height: 300px;
  margin: 0px auto;
  background: transparent;
  border-radius: 6px;

  display: flex;
  flex-direction: row;
}

.product {
  cursor: pointer;
  height: 250px;
  padding:10px;
  margin: 0 10px;
  display: flex;
  flex: 1;
  background: white;
  border-radius:  10px;
  box-shadow: 1px 2px 3px 0px rgba(0,0,0,0.10);
  flex-direction: column;
  
}

.product:hover{
  transform: scale(1.05);
}

.product-image {
  height: 70%;
  max-width:100%;
max-height:100%;
object-fit: contain;
}

.info {
  display: flex;
  text-align: center;
  flex-direction: column;

}


.shopping-cart {
  width: 750px;
  height: 423px;
  margin: 20px auto 20px auto;
  background: #FFFFFF;
  box-shadow: 1px 2px 3px 0px rgba(0,0,0,0.10);
  border-radius: 6px;

  display: flex;
  flex-direction: column;
}

.title {
  height: 60px;
  border-bottom: 1px solid #E1E8EE;
  padding: 20px 30px;
  color: #5E6977;
  font-size: 18px;
  font-weight: 400;
  text-align: center;
}

.item {
  padding: 20px 30px;
  height: 120px;
  display: flex;
}

.item:nth-child(3) {
  border-top:  1px solid #E1E8EE;
  border-bottom:  1px solid #E1E8EE;
}

/* Buttons -  Delete and Like */
.buttons {
  position: relative;
  padding-top: 30px;
  margin-right: 60px;
}

.delete-btn {
  display: inline-block;
  cursor: pointer;
  width: 18px;
  height: 17px;
  background: url("./images/delete-icn.svg") no-repeat center;
  margin-right: 20px;
}
 
.like-btn {
  position: absolute;
  top: 9px;
  left: 15px;
  display: inline-block;
  background: url('./images/twitter-heart.png');
  width: 60px;
  height: 60px;
  background-size: 2900%;
  background-repeat: no-repeat;
  cursor: pointer;
}

.is-active {
  animation-name: animate;
  animation-duration: .8s;
  animation-iteration-count: 1;
  animation-timing-function: steps(28);
  animation-fill-mode: forwards;
}

@keyframes animate {
  0%   { background-position: left;  }
  50%  { background-position: right; }
  100% { background-position: right; }
}

/* Product Image */
.image {
  max-width:100%;
  max-height:100%;
  object-fit: contain;
  margin-right: 50px;
}

/* Product Description */
.description {
  padding-top: 10px;
  margin-right: 60px;
  width: 115px;
}

.description span {
  display: block;
  font-size: 14px;
  color: #43484D;
  font-weight: 400;
}

.description span:first-child {
  margin-bottom: 5px;
}
.description span:last-child {
  font-weight: 300;
  margin-top: 8px;
  color: #86939E;
}

/* Product Quantity */
.quantity {
  padding-top: 20px;
  margin-right: 60px;
}
.quantity input {
  -webkit-appearance: none;
  border: none;
  text-align: center;
  width: 32px;
  font-size: 16px;
  color: #43484D;
  font-weight: 300;
}

button[class*=btn] {
  width: 30px;
  height: 30px;
  background-color: #E1E8EE;
  border-radius: 6px;
  border: none;
  cursor: pointer;
}
.minus-btn img {
  margin-bottom: 3px;
}
.plus-btn img {
  margin-top: 2px;
}
button:focus,
input:focus {
  outline:0;
}

/* Total Price */
.total-price {
  width: 83px;
  padding-top: 27px;
  text-align: center;
  font-size: 16px;
  color: #43484D;
  font-weight: 300;
}

.checkout-button{
  width: 750px;
  height: 80px;
  display: flex;
  flex-direction: row;
  margin: 10px auto;
  border-radius: 10px;

  background: #f857a6;  /* fallback for old browsers */
background: -webkit-linear-gradient(to right, #ff5858, #f857a6);  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to right, #ff5858, #f857a6); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

}

.checkout-button > h3{
  flex:1;
  color: #FFFFFF;
  margin: auto 20px;
}

.checkout-button > h2{
  flex:1;
  text-align: right;
  color: #FFFFFF;
  margin: auto 50px;
}

/* Responsive */
@media (max-width: 800px) {

  .product-list {
    width: 100%;
    height: auto;
    overflow: hidden;
    flex-direction: column;
  }

  .checkout-button{
    width: 100%;
    overflow: hidden;
  }

  .product{
    margin: 10px 0;
  }

  .shopping-cart {
    width: 100%;
    height: auto;
    overflow: hidden;
  }
  .item {
    height: auto;
    flex-wrap: wrap;
    justify-content: center;
  }
  .image img {

    width: 50%;
  }
  .image,
  .quantity,
  .description,.info {
    width: 100%;
    text-align: center;
    margin: 6px 0;
  }
  .buttons {
    margin-right: 20px;
  }
}
